































.backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
}

.modal {
  padding: 40px;
  border-radius: 6px;
  background: #fff;
  border: 1px solid #eee;
  color: #0a2b4e;
}
