










































































.remove-item {
  display: inline-block;
  margin-left: 5px;
  color: red;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}

.item-wrapper {
  position: relative;
  margin-bottom: 10px;
}

.item-text {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
}

.text-editable {
  display: inline-block;
  margin-right: 5px;
  cursor: pointer;
}
